let need_update_var = true;
const MAX_QUALITY = 999999999;

function seek(e) {
	var v = e.target.value;
	fetch("/api/seek/abs/" + v, { method: "POST" });
}

function vol(e) {
	var v = e.target.value;
	fetch("/api/vol/abs/" + v, { method: "POST" });
}

function hms(s) {
	if (s === null || s === undefined) return "--:--";
	var h = Math.floor(s / 3600);
	s = s % 3600;
	var m = Math.floor(s / 60);
	s = Math.floor(s % 60);
	return h.toString() + ":" + m.toString().padStart(2, "0") + ":" + s.toString().padStart(2, "0");
}

function instance($$self, $$props, $$invalidate) {
	let state = {
		title: "KEK",
		video: "kek",
		position: 42,
		duration: 212,
		volume: 73,
		playing: false,
		mute: false,
		playlist: [],
		audio_tracks: [],
		sub_tracks: []
	};

	let newurl = "https://www.youtube.com/watch?v=LXb3EKWsInQ";
	let n = 0;

	need_update.subscribe(v => {
		if (!v) return;

		fetch("/api/status2").then(resp => resp.json()).then(js => {
			$$invalidate(0, state = js);
			need_update.set(false);
			updateMetadata(state);
		});
	});

	setInterval(
		() => {
			fetch("/api/status").then(resp => resp.json()).then(js => {
				if (js.playlist_pos != state.playlist_pos) need_update.set(true);
				for (var key in js) $$invalidate(0, state[key] = js[key], state);
				updateMetadata(state);
			});

			n = n > 10 ? 0 : n + 1;
			if (n === 0) need_update.set(true);
		},
		250
	);

	need_update.set(true);
	
	
	
	const QUALITY_VARIANTS = [MAX_QUALITY, 2160, 1080, 720, 480, 360, 240, 144];

	function input0_input_handler() {
		newurl = this.value;
		$$invalidate(1, newurl);
	}

	const click_handler = (q, e) => fetch("/api/maxres/" + q, { method: "POST" });
	return [state, newurl, QUALITY_VARIANTS, input0_input_handler, click_handler];
}

class App extends SvelteComponent {
	constructor(options) {
		super();
		init(this, options, instance, create_fragment, safe_not_equal, {});
	}
}

export default App;